@charset "UTF-8";

#coaches {
    background-color: map-get($colors, "bp3");

    .tmm-row {
        text-align: center;

        &-main {

        }

        .item-square-3 {
            background-color: map-get($colors, "b_w");
            display: inline-block;
            overflow: hidden;
            margin: 5px;
            padding: 10px;
            position: relative;
            text-align: center;
            width: 274px;

            box-shadow: 5px 5px 5px rgba(map-get($colors, "bs1"), 0.4);
            transition: all .3s ease;

            &:hover {
                box-shadow: 5px 5px 3px rgba(map-get($colors, "bs1"), 0.6);
            }

            &:nth-child(2n) {
                background-color: map-get($colors, "bs3");

                .item-content {

                    h4, .text p {
                        /*color: map-get($colors, "b_w");*/
                    }
                }
            }

            &:hover {

                .item-content {

                     .text p {
                        transform: translateY(-80%);
                        transition: transform 6s linear;
                    }
                }
            }

            .face-container {
                padding: 0;
                position: relative;
                overflow: hidden;

                &:hover {

                    .social-icons {
                        transform: translateX(0px);
                        transition: all 700ms cubic-bezier(0.680, -0.550, 0.265, 1.550);
                    }
                }

                .face {
                    margin: 0 auto;
                    border: none;
                    height: 240px;

                    &:hover {

                         img {
                            transform: scale(1.2);
                            transition: transform .35s ease;
                        }
                    }

                    img {
                        border: 0;
                        display: block;
                        height: 100%;
                        max-width: 100%;
                        transition: transform .35s ease;
                    }
                }

                .social-icons {
                    background-color: map-get($colors, "bs2");
                    padding: 7px 12px;
                    position: absolute;
                    bottom: 0;
                    right: 0;
                    transform: translateX(145px);
                    transition: all 700ms cubic-bezier(0.680, -0.550, 0.265, 1.550);

                    li {
                        display: inline-block;
                        list-style: none;

                        &:hover {
                            &:nth-child(1) {
                                a {
                                    color: map-get($colors, "ot1");
                                }
                            }
                            &:nth-child(2) {
                                a {
                                    color: map-get($colors, "ot2");
                                }
                            }
                            &:nth-child(3) {
                                a {
                                    color: map-get($colors, "ot3");
                                }
                            }
                        }

                        a {
                            color: map-get($colors, "b_w");
                            font-size: 24px;
                            cursor: pointer;

                            &.btn {
                                color: map-get($colors, "bp1");
                                font-size: 0.8em;
                                padding: 5px 15px;

                                &:hover {
                                    color: map-get($colors, "b_w");
                                }
                            }
                        }
                    }
                }
            }

            .item-content {
                color: map-get($colors, "bs1");
                font-family: "Open Sans", sans-serif;
                font-weight: 400;
                text-align: left;

                h4 {
                    /*font-size: 18px;*/
                    text-transform: uppercase;
                    padding-top: 14px;
                }

                .text {
                    display: block;
                    overflow: hidden;
                    width: 100%;
                    /*max-height: 60px;*/
                    max-height: 110px;

                    p {
                        font-size: 12px;
                        color: map-get($colors, "bs1");
                        text-align: left;
                        padding-bottom: 10px;
                        transition: transform 1s linear;
                    }
                }
            }
        }
    }

}
